chooseIndex(index) {

for (var i = 0; i < this.ticArr.length; i++) {
if (index === i) {
this.ticArr[i].ticObj.checked = true
} else {
this.ticArr[i].ticObj.checked = false
}
}
this.ticIndex = index;
