export default {
  name: "Binsert",
  data() {
    return {}
  },
  methods: {
    bottomBtn() {//
      this.$emit('btn-click')
    },
    wantGet() {//
      this.$emit('wantGet')
    }
  },
  props: {
    leastNum: {
      type: Number,
      default: 1
    },
    type: {
      type: Number,
      default: 0
    },
    tType: {
      type: String,
      default: 'time'
    },
    leftTime: {
      type: String,
      default: "14:00"
    }
  }
}



